.header {
  display: flex;
  height: 50px;
  justify-content: space-between;
  align-items: center;
  text-align: center;
  padding: 0 2%;
  border-radius: 24px 24px 0 0;
  background-color: #1c1c1c;
  color: #adadad;

  a:link,
  a:visited,
  button {
    color: #adadad;
  }
  .menu-items {
    display: flex;
    list-style: none;
    font-size: 10px;
    li {
      margin-left: 20px;
    }
  }
  .header_contact-button {
    background-color: transparent;
    text-transform: uppercase;
    text-decoration: underline;
  }
}
