.main {
  .section_one-body {
    height: 560px;
    background-image: url(/img/toppng.com-engine-png-1000x1000.png);
    background-repeat: no-repeat;
    background-color: #1c1c1c;

    background-size: contain;
    background-position: center;

    position: relative;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    color: #fff;

    a:link,
    a:visited {
      color: #adadad;
    }
    .section_one-h2 {
      font-size: 105px;
      text-transform: uppercase;
      font-weight: 300;
      letter-spacing: 5px;
      text-shadow: 1px 1px #000;
    }
    .section_one-button {
      margin-top: 100px;
      background-color: #f0efef;
      border-radius: 24px;
      padding: 10px 20px;
    }
    .section_img {
      position: absolute;
      img {
        width: 100px;
      }
    }
    .section_one-autograf {
      top: 80px;
      left: 300px;
      transform: rotate(340deg);
    }
    .section_one-umbrella {
      top: 30px;
      left: 450px;
    }
  }
}
