.footer {
  display: flex;
  height: 80px;
  justify-content: space-between;
  align-items: center;
  text-align: center;
  padding: 0 2%;
  background-color: #1c1c1c;
  border-radius: 0 0 24px 24px;
  display: flex;
  a:link,
  a:visited {
    color: #8a8888;
  }
  .footer_content {
    width: 30%;
    font-size: 8px;
    color: white;
    text-align: initial;
    line-height: 1.5;
  }
  .fuuter_buttons {
    display: flex;

    a {
      border: #8a8888 solid 1px;
      border-radius: 24px;
      padding: 5px 10px;
      margin: 0;
      display: flex;
      justify-content: center;
      margin-left: 10px;
    }
  }
}
